<template>
  <div class="page">
    <div class="page__content">
      <div class="page__header">
        <h1 class="page__title">
          Анкета арт-резидента
        </h1>
      </div>

      <div class="resident-profile" v-show="load">
        <div class="resident-profile__left-col">
          <div class="resident-profile__photo">
            <svg>
              <use href="@/assets/img/sprite.svg#icon-megaphone-speaker"></use>
            </svg>
          </div>

          <div class="resident-profile__group-btn" v-if="state === 3">
            <button
              class="button-warning"
              @click="$router.push('/create-event')"
            >
              <svg>
                <use href="@/assets/img/sprite.svg#icon-calendar-star"></use>
              </svg>
              Добавить событие
            </button>
            <button
              class="button-primary mt-10"
              @click="$router.push('/create-startup')"
            >
              <svg>
                <use href="@/assets/img/sprite.svg#icon-rocket"></use>
              </svg>
              Запустить арт-стартап
            </button>
          </div>
        </div>
        <form class="resident-profile__form" @submit.prevent>
          <div
            class="resident-profile__info resident-profile__info--warning"
            v-if="state === 2"
          >
            <div class="resident-profile__info-icon">
              <svg>
                <use href="@/assets/img/sprite.svg#icon-info"></use>
              </svg>
            </div>
            <div class="resident-profile__info-text">
              Заявка от {{ userRequest.created_at | fdate }} находится на
              рассмотрении
            </div>
          </div>
          <div
            class="resident-profile__info resident-profile__info--danger"
            v-if="state === 4"
          >
            <div class="resident-profile__info-icon">
              <svg>
                <use href="@/assets/img/sprite.svg#icon-info"></use>
              </svg>
            </div>
            <div class="resident-profile__info-text">
              Заявка от {{ userRequest.created_at | fdate }} отклонена
            </div>
          </div>
          <div
            class="resident-profile__info resident-profile__info--success"
            v-if="state === 3"
          >
            <div class="resident-profile__info-icon">
              <svg>
                <use href="@/assets/img/sprite.svg#icon-info"></use>
              </svg>
            </div>
            <div class="resident-profile__info-text">
              Ваша заявка одобрена
            </div>
          </div>
          <v-input
            :label="'Имя'"
            :error="$v.form.firstname.$error"
            v-model="$v.form.firstname.$model"
            :text-error="'введите имя'"
            read-only
            require-input
          />
          <v-input
            :label="'Фамилия'"
            :error="$v.form.lastname.$error"
            v-model="$v.form.lastname.$model"
            :text-error="'введите фамилию'"
            read-only
            require-input
          />
          <v-input
            :label="'Телефон'"
            :error="$v.form.phone.$error"
            v-model="$v.form.phone.$model"
            :mask="'+7 (###) ###-##-##'"
            :text-error="'введите телефон'"
            read-only
            require-input
          />
          <v-input
            :label="'E-mail'"
            :error="$v.form.email.$error"
            v-model="$v.form.email.$model"
            :text-error="'введите e-mail'"
            read-only
            require-input
          />
          <v-multiselect
            :label="'Тип проекта'"
            :options="CATEGORY"
            :value="form.categories"
            @multi-change="form.categories = $event"
            :show-error="$v.form.categories.$invalid"
            :error-text="'укажите тип проекта'"
            require-input
          ></v-multiselect>
          <v-input :label="'Ссылка на проект'" v-model="form.projectUrl" />
          <div
            class="form-control"
            :class="{ 'form-control--error': $v.form.about.$error }"
          >
            <vue-editor
              v-model="$v.form.about.$model"
              :editorToolbar="customToolbar"
            ></vue-editor>
            <label v-show="!$v.form.about.$error">
              О себе
              <span class="form-control__require">*</span>
            </label>
            <label v-show="$v.form.about.$error"
              >укажите информацию о себе</label
            >
          </div>
          <div class="resident-profile__form-group-btn" v-if="this.state === 1">
            <button class="button-primary" @click="onCreateResident">
              Отправить на проверку
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { required, minLength, email } from "vuelidate/lib/validators";
import VMultiselect from "../components/vMultiselect";
import axios from "axios";
import qs from "qs";
import { VueEditor } from "vue2-editor";
import mixin from "../mixin/mixin";
import VInput from "../components/vInput";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "ResidentProfilePage",
  components: { VMultiselect, VInput, VueEditor },
  mixins: [mixin],
  data() {
    return {
      load: false,
      form: {
        id: 0,
        username: "",
        firstname: "",
        lastname: "",
        phone: "",
        email: "",
        categories: [],
        projectUrl: "",
        about: ""
      },
      userRequest: null,
      category: [],
      customToolbar: [
        ["bold", "italic", "underline"],
        [{ list: "ordered" }, { list: "bullet" }]
      ]
    };
  },
  validations: {
    form: {
      firstname: {
        required
      },
      lastname: {
        required
      },
      phone: {
        required,
        minLength: minLength(18)
      },
      about: {
        required
      },
      email: {
        required,
        email
      },
      categories: {
        required
      }
    }
  },
  async mounted() {
    let load = this.$loading.show();
    this.load = false;
    const user = this.$store.getters["user/getUser"];
    await this.loadData();
    await this.GET_CATEGORY();
    this.form = {
      ...user,
      about: this.userRequest ? this.userRequest.author.about : "",
      phone: user.phone ? String(user.phone).slice(1) : "",
      categories: this.userRequest ? this.userRequest.categories : [],
      projectUrl: this.userRequest ? this.userRequest.projectUrl : ""
    };
    this.load = true;
    load.hide();
  },
  methods: {
    ...mapActions("category", ["GET_CATEGORY"]),
    async loadData() {
      await axios
        .get(`${process.env.VUE_APP_API_URL}/v1/resident-request/user-request`)
        .then(response => {
          if (response.data.length > 0) {
            this.userRequest = response.data.slice(-1)[0];
          } else {
            this.userRequest = null;
          }
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
          }
          this.$toasted.global.error(error.response.data.message);
        });
    },
    async onCreateResident() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        let form = qs.stringify({
          ...this.form,
          categories: this.form.categories.map(item => item.id),
          projectUrl: this.form.projectUrl
        });
        await axios
          .post(
            `${process.env.VUE_APP_API_URL}/v1/resident-request/create`,
            form
          )
          .then(response => {
            this.userRequest = response.data;
            this.$toasted.global.success("Данные обновлены");
          })
          .catch(error => {
            if (!error.response) {
              this.$toasted.global.error("Нет соединения с сервером");
            }
            if (Array.isArray(error.response.data)) {
              error.response.data.forEach(item => {
                this.$toasted.global.error(item.message);
              });
            }
          });
      }
    }
  },
  computed: {
    ...mapGetters("category", ["CATEGORY"]),
    state() {
      // 1 - заявка не создана, 2 - заявка на рассмотрении, 3 -  заявка одобрена , 4 -  заявка не одобрена
      if (this.userRequest === null) {
        return 1;
      }
      if (this.userRequest && this.userRequest.confirmed === 0) {
        return 2;
      }
      if (this.userRequest && this.userRequest.confirmed === 1) {
        return 3;
      }
      if (this.userRequest && this.userRequest.confirmed === 2) {
        return 4;
      }
      return 0;
    }
  }
};
</script>
